<script>
  import Chart from 'chart.js'
  import ChartDataLabels from 'chartjs-plugin-datalabels'
  import * as Calc from '@/calculator'

  Chart.plugins.unregister(ChartDataLabels)

  const config = {
    type: 'scatter',
    plugins: [ChartDataLabels],
    data: {
      labels: [],
      datasets: [
        {
          fill: false,
          backgroundColor: 'white',
          borderColor: 'white',
          data: [],
          showLine: true,
          lineTension: 0.4,
          borderWidth: 2,
          showTooltip: false,
          pointRadius: 3,
          datalabels: {
            align: 'bottom',
            color: 'black',
            backgroundColor: 'rgba(255, 255, 255, 0.7)',
            formatter: function(value, context) {
              return Math.round(value.y) + ' RPM'
            }
          }
        }
      ].concat(new Array(Calc.NUM_GEARS).fill(0).map((e, i) => i)
        .map(i => {
          return {
            label: Calc.GEARS[i].label,
            fill: false,
            backgroundColor: Calc.GEARS[i].color,
            borderColor: Calc.GEARS[i].color,
            data: [],
            showLine: true,
            datalabels: {
              display: false
            }
          }
        }))
    },
    options: {
      responsive: true,
      // title: {
      //   display: true,
      //   text: 'Results'
      // },
      legend: {
        display: false
      },
      scales: {
        xAxes: [{
          display: true,
          scaleLabel: {
            display: false,
            labelString: 'RPM'
          }
        }],
        yAxes: [{
          display: true,
          scaleLabel: {
            display: false,
            labelString: 'MPH'
          },
          ticks: {
            beginAtZero: true,
            steps: 10,
            stepValue: 5
            // max: 200
          }
        }]
      },
      hover: {
        // these values should match tooltip
        mode: 'index',
        axis: 'y',
        intersect: false
      },
      elements: {
        point: {
          // hide points
          radius: 0
        },
        line: {
          // disables bezier curves
          tension: 0,
          borderWidth: 3
        }
      },
      tooltips: {
        enabled: true,
        mode: 'index',
        axis: 'y',
        intersect: false,
        titleFontFamily: '"SF Mono", "Monaco", "Inconsolata", "Fira Mono", "Droid Sans Mono", "Source Code Pro", monospace',
        bodyFontFamily: '"SF Mono", "Monaco", "Inconsolata", "Fira Mono", "Droid Sans Mono", "Source Code Pro", monospace',
        custom(tooltip) {
          const title = tooltip.title
          if (!title || title.length === 0) return

          tooltip.titleFontColor = parseInt(title[0]) >= this._data.redLine
            ? '#ff0000'
            : '#ffffff'
        },
        callbacks: {
          title(item) {
            return `${item[0].yLabel} RPM`
          },
          label(item, data) {
            const dataset = data.datasets[item.datasetIndex]
            const precision = 2
            const value = Calc.round(dataset.data[item.index].x, precision)

            const maxDigits = data.datasets.map(dataset => {
              if (!dataset) return 0
              const point = dataset.data[item.index]
              if (!point) return 0
              return parseInt(point.x).toString().length
            }).sort().slice(-1)[0] +
              // allow for decimals and period
              precision + 1
            return `${dataset.label}: ${value.padStart(maxDigits, ' ')}`
          }
        },
        filter: function(tooltipItem) {
          return tooltipItem.datasetIndex > 0
        }
      }
    }
  }

  /*
  Chart.plugins.register({
    beforeDraw(chart) {
      return
      const ctx = chart.chart.ctx
      const area = chart.chartArea

      const left = (area.right + 7) * (chart.data.redLine / chart.data.maxRPM)

      ctx.save()
      // chart.config.options.chartArea.backgroundColor
      ctx.fillStyle = 'rgba(251, 85, 85, 0.4)'
      ctx.fillRect(left, area.top, area.right - left, area.bottom - area.top)
      ctx.restore()
    }
  })
  */

  function updateChart(chart, props) {
    // update x-axis labels
    const step = 100
    const extra = 0
    const end = props.redLine + extra
    const numSteps = (end / step) + 1
    const steps = Array(numSteps).fill(0)

    // chart.options.scales.yAxes[0].scaleLabel.labelString = props.unit.label

    chart.data.redLine = props.redLine
    chart.data.maxRPM = end
    // chart.data.labels = steps.map((x, i) => String(i * step))

    // update data
    const values = props.values.map(value => {
      if (value === 0) return null
      return steps
        .map((x, i) => {
          const t = 1 - ((step * i) / end)
          return { x: value * t, y: end / 1 - (step * i) }
        })
        .reverse()
    })

    const dropRatios = props.values.map((value, i) => {
      const nextValue = props.values[i + 1]
      if (!value || !nextValue) return
      return value / nextValue
    })

    for (let i = 0; i < values.length; i++) {
      // account for drop dataset
      const datasetIndex = i + 1
      let data = values[i]

      if (!data) {
        chart.data.datasets[datasetIndex].borderWidth = 0
        continue
      }

      const ratio = dropRatios[i]
      if (ratio) {
        data.push({ x: data[data.length - 1].x, y: Math.round(props.redLine * ratio) })
      }

      chart.data.datasets[datasetIndex].data = data
    }

    chart.data.datasets[0].data = dropRatios.map((ratio, i) => {
      return { x: props.values[i], y: props.redLine * ratio }
    })

    chart.update()
  }

  export default {
    name: 'chart',
    props: ['values', 'redLine', 'unit'],
    data() {
      return {}
    },
    mounted() {
      const ctx = document.getElementById('chart').getContext('2d')
      this.chart = new Chart(ctx, config)
      updateChart(this.chart, this.$props)
    },
    watch: {
      values() {
        updateChart(this.chart, this.$props)
      }
      // unit() {
      //   updateChart(this.chart, this.$props)
      // }
    }
  }
</script>

<template>
  <div id="container">
    <canvas id="chart"></canvas>
  </div>
</template>

<style scoped>
</style>



