export const NUM_GEARS = 6
export const RPM_STEP = 100
export const MIN_RPM = 5000
export const MAX_RPM = 10000

export const UNITS = [
  { id: 'mph', label: 'MPH', multiplier: 1 },
  { id: 'kph', label: 'KM/H', multiplier: 1.60934 }
]

export const GEARS = [
  { label: '1st', color: '#ff6384' },
  { label: '2nd', color: '#ff9f40' },
  { label: '3rd', color: '#ffcd56' },
  { label: '4th', color: '#4bc0c0' },
  { label: '5th', color: '#36a2eb' },
  { label: '6th', color: '#9966ff' }
]

export const DEBUG_DATA = {
  tyreWidth: 235,
  tyreProfile: 45,
  rimSize: 17,
  redLine: 7500,
  finalDrive: 4.307,
  tyreDiameter: 25.33,
  unitId: UNITS[0].id,
  inputMode: 0,
  gears: [
    { name: '1st', value: 3 },
    { name: '2nd', value: 2 },
    { name: '3rd', value: 1.47 },
    { name: '4th', value: 1.11 },
    { name: '5th', value: 0.857 },
    { name: '6th', value: 0 }
  ]
}

export function round(number, precision) {
  return number.toFixed(precision)
}

export function calculateTyreDiameter(tyreWidth, tyreProfile, rimSize) {
  return ((tyreWidth * (tyreProfile / 100) / 25.4) * 2) + rimSize
}

export function calculateTyreCircumference(diameter) {
  return diameter * Math.PI
}

export function calculateGearSpeeds(circumference, redLine, finalDrive, ratios) {
  if (!circumference || !redLine || !finalDrive || !ratios || ratios.length !== NUM_GEARS) {
    return new Array(NUM_GEARS).fill(0)
  }
  return ratios.map(ratio => {
    if (!ratio) {
      return 0
    }
    return ((redLine / (ratio * finalDrive) * circumference) * 60) / 63360
  })
}



