<template>
  <div class="form-group">
    <label for="" v-if="label" class="label">{{ label }}</label>
    <div class="input-group">
      <input class="form-control" type="number" v-bind:step="step" v-bind:style="style" v-bind:placeholder="placeholder"
             v-model.number="internalValue">
      <div v-if="unit" class="input-group-append">
        <span class="input-group-text">{{ unit }}</span>
      </div>
    </div>
    <small v-if="help" class="form-text text-muted">
      {{ help }}
    </small>
  </div>
</template>

<script>
  function formattedValue(value) {
    return value === '' ? null : value
  }

  export default {
    name: 'number-input',
    props: ['label', 'placeholder', 'unit', 'step', 'help', 'color', 'value'],
    data() {
      return {
        internalValue: null
      }
    },
    computed: {
      style() {
        // return `border: 1px solid ${this.color}`
        return `border: 1px solid ${this.color}`
      }
    },
    // https://stackoverflow.com/a/40915857
    watch: {
      internalValue() {
        this.$emit('input',
          // prevent empty string
          formattedValue(this.$data.internalValue))
      }
    },
    created() {
      this.internalValue = this.value
    }
  }
</script>

<style scoped>
</style>



