<template>
  <div>
    <div class="card">
      <div class="card-header">
        <ul class="nav nav-tabs card-header-tabs">
          <li class="nav-item" v-for="mode in INPUT_MODES">
            <a class="nav-link" href="#" v-on:click.prevent="inputMode = mode.id"
               v-bind:class="{ active: inputMode === mode.id }">
              {{ mode.title }}
            </a>
          </li>
        </ul>
      </div>
      <div class="card-body">
        <div class="form-row" v-show="inputMode === INPUT_MODES[0].id">
          <div class="col col-sm-6 col-lg-4">
            <number-input label="Tyre Diameter" unit="Inches" v-model.number="tyreDiameter"/>
          </div>
        </div>
        <div class="form-row" v-show="inputMode === INPUT_MODES[1].id">
          <div class="col-12 col-sm-4">
            <number-input label="Tyre Width" unit="mm" v-model.number="tyreWidth"/>
          </div>
          <div class="col-12 col-sm-4">
            <number-input label="Tyre Profile" unit="mm" v-model.number="tyreProfile"/>
          </div>
          <div class="col-12 col-sm-4">
            <number-input label="Rim Size" unit="Inches" v-model.number="rimSize"/>
          </div>
        </div>
      </div>
    </div>
    <div class="card">
      <div class="card-body">
        <div class="form-row">
          <div class="col col-sm-6 col-lg-4">
            <div class="form-group">
              <label>Redline</label>
              <div class="input-group">
                <select class="custom-select" name="" id="red-line" v-model.number="redLine">
                  <option v-for="value in rpmValues" v-bind:value="value">{{ value }}</option>
                </select>
                <div class="input-group-append">
                  <span class="input-group-text">Max RPM</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="form-row">
          <div class="col col-sm-6 col-lg-4">
            <number-input label="Final Drive" unit=":1" step="0.1" v-model="finalDrive"/>
          </div>
        </div>
        <div class="form-row">
          <div class="col-6 col-md-4 col-lg-2" v-for="(gear, index) in gears">
            <number-input v-bind:label="GEARS[index].label" v-bind:color="GEARS[index].color" unit=":1" step="0.1"
                          v-model="gear.value"/>
          </div>
        </div>
      </div>
    </div>
    <div id="results-container" class="card">
      <div class="card-header">
        <ul class="nav nav-pills card-header-pills justify-content-end">
          <li class="nav-item" v-for="unit in UNITS">
            <a class="nav-link" href="#" v-on:click.prevent="unitId = unit.id"
               v-bind:class="{ active: unitId === unit.id }">
              {{ unit.label }}
            </a>
          </li>
        </ul>
      </div>
      <div class="card-body">
        <chart id="chart-container" v-bind:values="gearSpeeds" v-bind:redLine="redLine" v-bind:unit="unit"/>
      </div>
    </div>
  </div>
</template>

<script>
  import NumberInput from '@/components/NumberInput'
  import Chart from '@/components/Chart'
  import * as Calc from '@/calculator'

  export default {
    name: 'calculator',
    data() {
      return Object.assign({}, Calc.DEBUG_DATA)

      /*
      return {
        tyreWidth: 0,
        tyreProfile: 0,
        rimSize: 0,
        redLine: 0,
        finalDrive: 0,
        gears: [
          {name: '1st', value: 0},
          {name: '2nd', value: 0},
          {name: '3rd', value: 0},
          {name: '4th', value: 0},
          {name: '5th', value: 0},
          {name: '6th', value: 0}
        ]
      }
      */
    },
    computed: {
      gearSpeeds() {
        const diameter = this.$data.inputMode === this.INPUT_MODES[1].id
          ? Calc.calculateTyreDiameter(this.tyreWidth, this.tyreProfile, this.rimSize)
          : this.$data.tyreDiameter

        const circumference = Calc.calculateTyreCircumference(diameter)
        return Calc.calculateGearSpeeds(circumference, this.redLine, this.finalDrive, this.gears.map(x => x.value))
          .map(x => x * this.unit.multiplier)
      },
      rpmValues() {
        const range = Calc.MAX_RPM - Calc.MIN_RPM
        const steps = (range / Calc.RPM_STEP) + 1
        return Array(steps).fill(0)
          .map((x, i) => Calc.MIN_RPM + (Calc.RPM_STEP * i))
      },
      unit() {
        return Calc.UNITS.filter(x => x.id === this.unitId)[0]
      }
    },
    created() {
      this.UNITS = Calc.UNITS
      this.GEARS = Calc.GEARS
      this.INPUT_MODES = [
        { id: 0, title: 'Diameter' },
        { id: 1, title: 'Calculate Diameter' }
      ]
    },
    methods: {},
    components: {
      NumberInput,
      Chart
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .card {
    margin-top: 1rem;
  }
</style>



